import { memo, useEffect, useState } from 'react'
import { Image, Avatar, Divider, Toast } from 'antd-mobile'
import {
  LeftOutline,
  HeartOutline,
  HeartFill,
  StarOutline,
  StarFill,
  MessageOutline,
} from 'antd-mobile-icons'
import CommentList from './comment'
import { DetailWrapper } from './style'
import { useNavigate } from 'react-router-dom'
import { useDispatch, useSelector } from 'react-redux'
import {
  detailMoment,
  favorMoment,
  userCollect,
  userFollow,
} from '../../../redux/modules/home'
import formatdayjs from '../../../utils/date'
import { fetchuserdata } from '../../../redux/modules/user'
import getUserFromToken from '../../../components/takeuserfromtoken'
import isTokenExpired from '../../../components/istokenexpire'
const Detail = ({ id, onClose, show }) => {
  //获取经纬度
  // navigator.geolocation.getCurrentPosition(
  //   (position) => {
  //     const { latitude, longitude } = position.coords
  //     console.log(latitude, longitude)

  //   },
  //   (error) => {
  //     console.error('Error getting location:', error)
  //   }
  // )

  const dispatch = useDispatch()
  useEffect(() => {
    dispatch(detailMoment(id))
  }, [id, dispatch])
  useEffect(() => {
    if (getUserFromToken() && !isTokenExpired()) {
      dispatch(fetchuserdata())
    }
  }, [dispatch])
  const navigate = useNavigate()
  const res = useSelector((state) => state.home.itemdetail)
  const res2 = useSelector((state) => state.user.userdata.data) || []
  const res1 = res2[0]

  // 根据换行符分段
  const paragraphs = res.content?.split('\n').map((paragraph, index) => (
    <p key={index} style={{ textIndent: '2em' }}>
      {paragraph}
    </p>
  ))
  //处理点赞
  const userid = getUserFromToken()
  const [ownfavor, setOwnfavor] = useState()
  useEffect(() => {
    setOwnfavor(res?.favorCounts)
  }, [res?.favorCounts])

  const notLogin = isTokenExpired()
  const [isadd, setIsadd] = useState(true)
  useEffect(() => {
    setIsadd(!res?.favorUserIds?.includes(userid))
  }, [res?.favorUserIds, userid])

  const handleFavor = () => {
    if (!!userid && !notLogin) {
      dispatch(favorMoment(id))
      if (isadd) {
        setOwnfavor(ownfavor + 1)
        setIsadd(!isadd)
      } else {
        setOwnfavor(ownfavor - 1)
        setIsadd(!isadd)
      }
    } else {
      Toast.show({
        content: '先登陆，后点赞~',
      })
    }
  }
  //处理关注
  const [isfollow, setIsfollow] = useState(false)
  useEffect(() => {
    setIsfollow(res?.followUsers?.includes(userid))
  }, [res?.followUsers, userid])
  const handleFollow = () => {
    if (!!userid && !notLogin) {
      dispatch(userFollow({ id: userid, beFollowedUser: res.user.id }))
      setIsfollow((e) => !e)
    } else {
      Toast.show({
        content: '先登陆，后关注~',
      })
    }
  }
  //处理收藏
  const [iscollect, setIscollect] = useState(false)
  const [ownCollect, setOwnCollect] = useState()
  useEffect(() => {
    if (res?.collectUsers !== null) {
      setOwnCollect(res?.collectUsers?.length)
    } else {
      setOwnCollect(0)
    }
  }, [res?.collectUsers?.length, res?.collectUsers])
  useEffect(() => {
    setIscollect(res?.collectUsers?.includes(userid))
  }, [res?.collectUsers, userid])
  const handleCollect = () => {
    if (!!userid && !notLogin) {
      dispatch(userCollect({ id: userid, momentId: id }))
      if (!iscollect) {
        setOwnCollect(ownCollect + 1)
        setIscollect(!iscollect)
      } else {
        setOwnCollect(ownCollect - 1)
        setIscollect(!iscollect)
      }
    } else {
      Toast.show({
        content: '先登陆，后收藏~',
      })
    }
  }
  //右下角评论按钮
  const [isSay, setIsSay] = useState(false)
  const say = () => {
    setIsSay(!isSay)
  }

  return (
    <DetailWrapper>
      <div className="detail">
        {show && (
          <>
            <div className="top">
              <div className="topleft">
                <div onClick={() => onClose()}>
                  <LeftOutline style={{ marginRight: '20px' }} fontSize={24} />
                </div>
                <Avatar
                  src={res.user?.avatar}
                  fit="contain"
                  style={{ '--border-radius': '50%', '--size': '32px' }}
                />
                <div style={{ marginLeft: '10px', fontSize: '16px' }}>
                  {res?.user?.surname || res?.user?.name}
                </div>
              </div>
              <div
                className={isfollow ? 'topright1' : 'topright'}
                onClick={handleFollow}
              >
                {isfollow ? '已关注' : '关注'}
              </div>
            </div>
            <div style={{ height: '60px' }}></div>
          </>
        )}
        <div className="center">
          {show && (
            <div className="center-cover">
              <Image src={res.cover} />
            </div>
          )}
          <div>
            <div
              style={{
                marginBottom: '10px',
                fontWeight: 600,
                padding: '10px',
                paddingBottom: 0,
                fontSize: '18px',
              }}
            >
              {res.title}
            </div>
            <div
              style={{ margin: '0 auto', lineHeight: '1.6', fontSize: '16px' }}
            >
              {paragraphs}
            </div>
            <div style={{ marginLeft: '20px' }}>
              {res.labels?.map((item) => item?.name)}
            </div>
            <div
              style={{
                marginLeft: '20px',
                marginRight: '20px',
                display: 'flex',
                justifyContent: 'space-between',
              }}
            >
              <div> {formatdayjs(res.updateAt)}</div>
              <div>
                {userid === res?.user?.id && (
                  <div onClick={() => navigate('/edit')}>编辑文章</div>
                )}
              </div>
            </div>
            <Divider />
            <div style={{ marginLeft: '20px', marginRight: '20px' }}>
              <CommentList
                comments={res.comments}
                avaUrl={res1?.ava_url}
                id={id}
                isSay={isSay}
                setIsSay={setIsSay}
              />
            </div>
          </div>
        </div>
        {isTokenExpired() ? null : show ? (
          <div className="end">
            <input
              placeholder="说点什么"
              onClick={say}
              style={{
                paddingLeft: '20px',
                borderRadius: '16px',
                border: 'none',
                outline: 'none', // 隐藏聚焦时的外边框
                lineHeight: '32px',
                height: '32px',
                width: '100px',
                backgroundColor: '#f8f8f8',
              }}
            />
            <div
              style={{
                fontSize: '28px',
                display: 'flex',
                alignItems: 'center',
              }}
            >
              <div onClick={handleFavor}>
                {!!isadd ? <HeartOutline /> : <HeartFill color="red" />}
              </div>
              <span className="endfont">{ownfavor}</span>
              <div onClick={handleCollect}>
                {!iscollect ? (
                  <StarOutline style={{ paddingLeft: '10px' }} />
                ) : (
                  <StarFill
                    style={{ paddingLeft: '10px', color: ' #FFCC00' }}
                  />
                )}
              </div>
              <span className="endfont">{ownCollect}</span>
              <MessageOutline style={{ paddingLeft: '10px' }} onClick={say} />
              <span className="endfont">
                {res?.comments?.length === 1
                  ? res?.comments[0].id === null
                    ? 0
                    : 1
                  : res?.comments?.length}
              </span>
            </div>
          </div>
        ) : null}
      </div>
    </DetailWrapper>
  )
}
export default memo(Detail)
