import React, { useEffect, useRef, useState } from 'react'
import { Avatar, Space, Toast, Popup, List } from 'antd-mobile'
import { HeartOutline, HeartFill } from 'antd-mobile-icons'

import formatdayjs from '../../../utils/date'
import getUserFromToken from '../../../components/takeuserfromtoken'
import isTokenExpired from '../../../components/istokenexpire'
import { useDispatch } from 'react-redux'
import {
  comment,
  delComment,
  detailMoment,
  favorComment,
  replyComment,
} from '../../../redux/modules/home'

// 单个评论的组件，包括评论和其回复
const Comment = ({
  comment,
  comments,
  level = 0,
  id: momentId,
  setVisible1,
  focus,
  setfocus,
  del,
  setdel,
  specialCommentId,
  setSpecialCommentId,
}) => {
  const [reply, setReply] = useState('')

  const isArticleComment = comment?.commentId === null
  const id = comment?.id //判断是否有评论
  const ref = useRef()
  const dispatch = useDispatch()
  const handleReply01 = () => {
    if (!reply) {
      setfocus(false)
      return
    }
    dispatch(
      replyComment({
        content: reply,
        momentId: momentId,
        commentId: comment.id,
      })
    )
      .then(() => {
        dispatch(detailMoment(momentId))
      })
      .then(() => {
        setReply('')
        setfocus(false)
      })
  }
  const handleReply = () => {
    const userId = getUserFromToken()
    if (!userId || isTokenExpired()) {
      Toast.show('先登陆后对评论回复~')
      return
    }
    if (comment.commentuser.id !== userId) {
      ref.current.focus()
    } else {
      setVisible1(true)
    }
    setSpecialCommentId(comment.id)
  }
  useEffect(() => {
    if (focus) {
      ref.current.focus()
      setVisible1(false)
    }
  }, [focus])
  useEffect(() => {
    if (del && specialCommentId === comment.id) {
      dispatch(delComment({ commentId: specialCommentId })).then(() => {
        dispatch(detailMoment(momentId))
      })
      setdel(false)
      setSpecialCommentId(null)
    }
  }, [del])

  const [localfavor, setlocalfavor] = useState(comment?.favor)
  const [dofavor, setdofavor] = useState(false)

  const handlefavor = () => {
    if (dofavor) {
      setlocalfavor(localfavor - 1)
      setdofavor(false)
      dispatch(favorComment({ commentId: comment.id, favor: localfavor - 1 }))
    } else {
      setlocalfavor(localfavor + 1)
      setdofavor(true)
      dispatch(favorComment({ commentId: comment.id, favor: localfavor + 1 }))
    }
  }
  return (
    <>
      {!!id ? (
        <div
          key={comment?.id}
          style={{
            marginLeft: isArticleComment ? 0 : level === 1 ? '20px' : 0,
            lineHeight: '1.9',
          }}
        >
          {/* 评论的基本信息，如头像、用户名、内容等 */}
          <div
            style={{
              display: 'flex',
              justifyContent: 'space-between',
              flexShrink: '0',
            }}
          >
            <div style={{ display: 'flex', flexShrink: '0' }}>
              {/* 在这里添加你的头像和其他评论详细信息 */}
              {level >= 1 ? (
                <Avatar
                  src={comment?.commentuser?.avatar}
                  style={{ '--border-radius': '50%', '--size': '24px' }}
                />
              ) : (
                <Avatar
                  src={comment?.commentuser?.avatar}
                  style={{ '--border-radius': '50%', '--size': '32px' }}
                />
              )}

              <div style={{ flex: '1' }}>
                <div
                  style={{
                    paddingLeft: '10px',
                    paddingRight: '10px',
                  }}
                >
                  <div style={{ display: 'flex' }}>
                    {comment?.commentuser?.surname ||
                      comment?.commentuser?.name}
                    {level >= 2 && (
                      <div style={{ marginLeft: '10px', display: 'flex' }}>
                        回复
                        <div>
                          {comments?.filter((e) => {
                            return e.id === comment?.commentId
                          })[0].commentuser.surname ||
                            comments?.filter((e) => {
                              return e.id === comment?.commentId
                            })[0].commentuser.name}
                        </div>
                        :
                      </div>
                    )}
                  </div>
                  <div style={{ width: 260 }} onClick={handleReply}>
                    {comment?.content}
                    <span style={{ color: 'gray' }}>
                      <Space>
                        <span>{formatdayjs(comment?.createAt)}</span>
                        <span>回复</span>
                        <input
                          ref={ref}
                          type="text"
                          value={reply}
                          onChange={(e) => setReply(e.target.value)}
                          onBlur={handleReply01}
                          style={{
                            position: 'absolute',
                            left: '-9999px',
                            opacity: '0',
                          }}
                        />
                      </Space>
                    </span>
                  </div>
                </div>
              </div>
            </div>
            {/* 喜欢的图标和数量 */}
            <div
              style={{
                display: 'flex',
                flexDirection: 'column',
                alignItems: 'center',
              }}
            >
              <div onClick={handlefavor}>
                {dofavor ? (
                  <HeartFill fontSize={18} color="red" />
                ) : (
                  <HeartOutline fontSize={18} />
                )}
              </div>

              <div> {localfavor}</div>
            </div>
          </div>
          {/* 如果有回复，递归渲染回复 */}
          {comments
            ?.filter((reply) => reply?.commentId === comment?.id)
            ?.map((reply) => (
              <div key={reply?.id}>
                <Comment
                  key={reply?.id}
                  comment={reply}
                  comments={comments}
                  level={level + 1}
                  id={momentId}
                  setVisible1={setVisible1}
                  focus={focus}
                  setfocus={setfocus}
                  del={del}
                  setdel={setdel}
                  specialCommentId={specialCommentId}
                  setSpecialCommentId={setSpecialCommentId}
                />
              </div>
            ))}
        </div>
      ) : (
        <div style={{ marginTop: '30px', color: 'gray' }}>
          这里是一片荒草地，来评论一下吧~
        </div>
      )}
    </>
  )
}

// 评论列表的组件，渲染顶级评论及其回复
const CommentList = ({ comments, avaUrl, id, isSay, setIsSay }) => {
  const dispatch = useDispatch()
  const [commentContent, setCommentContent] = useState('')
  const [specialCommentId, setSpecialCommentId] = useState()
  const [visible1, setVisible1] = useState(false)
  const [focus, setfocus] = useState(false)
  const [del, setdel] = useState(false)

  const ref = useRef()
  useEffect(() => {
    if (isSay) {
      ref.current.focus()
      setIsSay(false)
    }
  }, [isSay])

  if (!comments || !Array.isArray(comments)) {
    return null // 如果评论数组为空或不是数组，直接返回 null 或其他你认为合适的内容
  }

  const handleComment = ({ content, momentId }) => {
    if (!isTokenExpired()) {
      if (!content) return
      dispatch(comment({ content, momentId })).then(() => {
        dispatch(detailMoment(id)).then(() => {
          setCommentContent('')
        })
      })
    }
  }
  return (
    <div>
      {/* 显示评论总数 */}
      <div>
        共
        {comments?.length === 1
          ? comments[0].id === null
            ? 0
            : 1
          : comments?.length}
        条评论
      </div>
      {/* 显示登陆的用户及评论输入框 */}
      <div>
        {!isTokenExpired() ? (
          <div
            style={{ marginTop: '30px', marginBottom: '30px', display: 'flex' }}
          >
            <Avatar
              src={avaUrl}
              style={{ '--border-radius': '50%', '--size': '32px' }}
            />
            <input
              ref={ref}
              placeholder="说点什么"
              value={commentContent}
              onChange={(e) => setCommentContent(e.target.value)}
              onBlur={(e) => {
                handleComment({ content: commentContent, momentId: id })
              }}
              style={{
                marginLeft: '10px',
                paddingLeft: '20px',
                borderRadius: '16px',
                border: 'none',
                outline: 'none', // 隐藏聚焦时的外边框
                lineHeight: '32px',
                height: '32px',
                flex: 1,
                backgroundColor: '#f8f8f8',
              }}
            />
          </div>
        ) : (
          <div
            style={{
              marginTop: '20px',
              marginBottom: '20px',
              color: 'grey',
              textAlign: 'center',
            }}
          >
            请登陆后发表评论吧~
          </div>
        )}
      </div>
      {/* 遍历顶级评论并渲染每个评论 */}
      {comments
        ?.filter((comment) => comment.commentId === null)
        ?.map((comment) => (
          <div key={comment?.id}>
            <Comment
              key={comment?.id}
              comment={comment}
              comments={comments}
              id={id}
              setVisible1={setVisible1}
              focus={focus}
              setfocus={setfocus}
              del={del}
              setdel={setdel}
              specialCommentId={specialCommentId}
              setSpecialCommentId={setSpecialCommentId}
            />
          </div>
        ))}
      <div
        style={{
          textAlign: 'center',
          marginTop: '20px',
          paddingBottom: '50px',
        }}
      >
        - END -
      </div>
      <Popup
        visible={visible1}
        onMaskClick={() => {
          setVisible1(false)
        }}
        onClose={() => {
          setVisible1(false)
        }}
        bodyStyle={{ height: '20vh' }}
      >
        <List style={{ textAlign: 'center' }}>
          <List.Item
            onClick={() => {
              setdel(true)
              setVisible1(false)
            }}
            arrow=""
          >
            删除
          </List.Item>
          <List.Item
            onClick={() => {
              setfocus(true)
              setVisible1(false)
            }}
            arrow=""
          >
            回复
          </List.Item>
        </List>
      </Popup>
    </div>
  )
}

export default CommentList
