import styled from 'styled-components'
export const DetailWrapper = styled.div`
  .detail {
    box-sizing: border-box;
    width: 100%;
    height: 100vh;

    .top {
      box-sizing: border-box;
      display: flex;
      width: 100%;
      justify-content: space-between;
      align-items: center;
      position: fixed;
      font-size: 22px;
      background-color: white;
      padding: 15px 20px;
      .topleft {
        display: flex;
        align-items: center;
        width: 210px;
      }
      .topright {
        border: 1px solid red;
        border-radius: 15px;
        padding: 6px 20px;
        width: 45px;
        height: 18px;
        color: red;
        font-size: 14px;
        line-height: 18px;
        text-align: center;
      }
      .topright1 {
        border: 1px solid red;
        border-radius: 15px;
        padding: 6px 20px;
        height: 18px;
        width: 45px;
        color: white;
        background-color: red;
        font-size: 14px;
        line-height: 18px;
        text-align: center;
      }
    }
    .center {
      overflow-y: auto;
      overflow-x: hidden;
      height: 780px;
    }
    .center::-webkit-scrollbar {
      display: none;
    }
    .end {
      display: flex;
      justify-content: space-between;
      align-items: center;
      position: fixed;
      padding: 0 20px;
      bottom: 0;
      left: 0;
      right: 0;
      height: 50px;
      background-color: white;
      border-top: 1px solid #f0f0f0;
      .endfont {
        font-size: 18px;
        line-height: 32px;
      }
    }
  }
`
